
import policy from '../../../static/js/returnPolicy'
import Bus from '../../assets/eventBus'
import Map from '../../public/map'
import { MessageBox } from 'mint-ui'
import Rater from 'vux/src/components/rater/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import LoadingImg from '../../public/loadingImg'
export default {
name: "mPublishPage",
data(){
return{
api: this.GLOBAL.baseURL,
height:"",
screnWidth:'',
opacity:0,
top:-50,
imageList:[
{img: '../../../static/img/static/2.jpg'},
{img: '../../../static/img/static/1.jpg'},
{img: '../../../static/img/static/3.jpg'},
],
disList:[],
houseList:[
{img: '../../../static/img/static/2.jpg'},
{img: '../../../static/img/static/1.jpg'},
{img: '../../../static/img/static/3.jpg'},
],
score:0,
bottom: -60,
jionDress:[],
activeData:'',
province:'',
imgList:[],
isOwer:"",
return_policy:'',
policy:[],
long_day:'',
houseImage:[]
}
},
components:{
LoadingImg,
Swiper,
SwiperItem,
Rater,
Cell,
Map
},
watch:{
'$route':'getParams',
province:function(){
this.$nextTick(()=>{
Bus.$emit('jionDress',[this.activeData.set_address_lng,this.activeData.set_address_lat])
Bus.$emit('tyas',2)
})
}
},
methods:{
openApp(){
var userAgent = navigator.userAgent ;
var url="",downUrl="";
if(userAgent.indexOf("Android")>-1){
url='/app-release.apk';
downUrl='/app-release.apk'; 
}else{
url='https://apps.apple.com/us/app/%E4%BA%BA%E4%BA%BA%E8%80%8D/id1482092521?l=zh&ls=1';
downUrl="https://apps.apple.com/us/app/%E4%BA%BA%E4%BA%BA%E8%80%8D/id1482092521?l=zh&ls=1"; 
}
window.open(downUrl)
},
checkApp(){
var userAgent = navigator.userAgent ;
var url="",downUrl="";
if(userAgent.indexOf("Android")>-1){
MessageBox.alert('暂时没有安卓APP，请耐心等待！')
}else{
url='https://apps.apple.com/us/app/%E4%BA%BA%E4%BA%BA%E8%80%8D/id1482092521?l=zh&ls=1';
downUrl="https://apps.apple.com/us/app/%E4%BA%BA%E4%BA%BA%E8%80%8D/id1482092521?l=zh&ls=1"; 
}
function test(){
window.location.href = url;
setTimeout(function(){
window.location.href = downUrl;
},5000)
}
test();
},
getComment(val){
this.$http.post(this.api + '/home/Comment/comment_list',{
token: localStorage.getItem('token'),
table_id:this.activity_id,
flag: 1,
order: 1,
page: val
})
.then(res=>{
if(res.data.code == 1){
let data = res.data.data.data
this.disList = data
this.total =  res.data.data.total
}else if(res.data.code == 3 || res.data.code == 4){
this.getComment(val)
}else if(res.data.code == 0){
alert(res.data.msg)
}
})
},
lookMoreImg(){
MessageBox.alert('请前往APP或者客户端查看')
},
goBack(){
this.$router.push('/')
},

getHeight(){
this.height = window.innerHeight
this.screnWidth = window.screen.width
},
getScroll(){
window.addEventListener('scroll',this.showHead)
},
showHead(){
let a =  window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
if(a>=0 && a <= 50){
this.top = a - 50
if(this.opacity){
this.opacity -= 0.1
}else{
this.opacity += 0.1
}
}else{
this.top = 0
this.opacity = 1
}
if(a>=0 && a <=60){
this.bottom = a - 60
}else{
this.bottom = 0
}
},
getParams(){
this.activity_id = this.$route.query.information
this.getActive()
this.getComment()
},
getActive(){
this.$http.post(this.api + '/home/Activity/get_activity',{
token: localStorage.getItem('token'),
activity_id: this.activity_id,
visit: 1
})
.then(res=>{
if(res.data.code == 1){
let data = res.data.data
this.activeData = data
this.imgList.push(data.cover)
this.score = data.score
for(let i =0;i<data.image.length;i++){
this.imgList.push(data.image[i])
}
if(this.isOwer){
var url =encodeURIComponent(window.location.href + '&'+ 'userId' +'='+this.isOwer)
}else{
var url =encodeURIComponent(window.location.href)
}
let obj = {
title: this.activeData.title,		//分享标题
desc: this.activeData.descripte,						//分享内容
linkurl: decodeURIComponent(url),//分享链接
img: this.imgList[0].domain + this.imgList[0].image_url,				//分享内容显示的图片
}
this.return_policy = this.policy[data.return_policy].label
this.jionDress = [data.set_address_lng,data.set_address_lat]
this.province = data.province +data.city+ data.region

if(data.issatay == 2){
this.long_day = '1'
this.houseImage = data.houseimage
}else if(data.issatay == 1){
this.long_day = '2'
var a = []
for(let i=0;i<data.house.length;i++){
a.push({img:data.house[i].image,flag:data.house[i].flag})
}
this.houseImage = a
}else{
this.long_day = ''
}
this.SDK(url,obj)
}else if(res.data.code== 3){
this.$http.post(this.api + '/home/index/token')
.then(res=>{
localStorage.setItem('token',res.data.data)
this.getActive()
})
}else if(res.data.code == 0){
MessageBox.alert(res.data.msg)
}
})
},
getUser(){
this.$http.post(this.api + '/home/User/get_user',{
token: localStorage.getItem('token')
})
.then(res=>{
if(res.data.code == 1){
this.isOwer = res.data.data.user_id
}
})
},
},
mounted(){
this.getScroll()
/*  this.getData()*/

},
created(){
this.getUser()
this.getParams()
this.getHeight()
window.scroll(0,0)
let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
if(!flag){
this.$router.push({
path: '/publishPage',
query:{
information: this.$route.query.information
}
})
}
this.policy = policy()
}
}



